import poshdtable from './pos_hdtable.js';
import { getPositionInfo } from './pos_info.js'
import { reRenderSelectOption, posType_name } from './pos_methods'
export const posMixin = {
  inject: ['reload'], //
  data() {
    return {
      hot: {},//hottable
      data: [],//hottabledata
      initList: {
        pos_folderList: {},//
        pos_machineList: {},//
        pos_dgmList: {},//
        pos_chaList: {},//name
        cap_chaList: {},//ID
        cap_sequenceNum: {},//
        rock_chaList: {},//
        mac_pos_id_list: [],//{[]}
        rock_pos_id_list: [],//pos_id
        rock_list:{},//
        task_idSources: {},// {dgm_id:{ch_id:{[task_id_sources],[indexs]}}}
        cc_mod_id_list: [],//
      },
      changeList: {
        changeData: {},//
        deleteData: [],//
        addData: {},//
      },
      timer: null,//
      scroll: 0,//
    }
  },
  //
  destroyed() {
    this.$store.commit('clearSroll', 'position');
    if(this.timer){
      clearTimeout(this.timer);
    }
  },
  //scoll
  activated() {
    let wtHolder = document.getElementsByClassName('wtHolder')[0]
    if (wtHolder) {
      let state = this.$store.state.scroll['position'][this.type]
      wtHolder.scrollTop = state === undefined ? 0 : state.scrollTop
      wtHolder.scrollLeft = state === undefined ? 0 : state.scrollLeft
    }
  },
  beforeRouteLeave (to, from, next) {
    let macRoute = ['CementMac','RailMac','OilMac','CommonMac'];//
    if((!$.isEmptyObject(this.changeList.changeData) || !$.isEmptyObject(this.changeList.addData)) && !macRoute.includes(to.name)){
      swal({
        title: "",
        text: `${posType_name(this.type)}`,
        buttons: {
          sure: "",
          cancel: "",
        },
        dangerMode: true,
      })
          .then((willDelete) => {
            if (willDelete) {
              next();
            } else {
              window.history.go(1);
            }
          });
    }
    else{
      next();
    }
  },
  mounted() {
    this.getListData(this.type)
  },
  methods: {
    getListData(type) {
      let loadTime = false;
      let that = this;
      this.timer = setTimeout(function(){//
        if(!loadTime){
          that.$parent.loading = true;//
        }
      },500)
      this.$getApi.queryPositionInfo(type, this.$store.state.tids)
        .then(res => {
          loadTime = true;
          this.$parent.loading = false;//
          let result = getPositionInfo(res, type)   //
          if (!result) {
            this.$emit("child-msg", this.type, false) //
          } else {
            let { data, invalidPos,...initList } = { ...result }
            if(invalidPos.length){
              // 
              this.$emit("invalidPosM", invalidPos)
            }
            this.data = data
            this.initList = initList
            //
            this.$emit("child-msg", this.type, true)
            // this.filter_by_dgmName()
            this.renderTable()
            /*  */
            if (this.data.length > 0) {
              let linkObj = [this.data[0].mac_id]
              this.$store.commit('setTreePosition', linkObj);
            }

          }
        })
          .catch(err=>{
            console.log('' ,err)
          })
    },
    /*  */
    renderTable() {
      if(this.$route.path.indexOf('position')<0){//
        return;
      }
      let HotTable = document.getElementById('HotTable');
      //table
      this.hot = new poshdtable(HotTable, this);
      /*  */
      reRenderSelectOption(this.hot, this.data, this.type)
      /*  */
      this.listenScroll()
      /*  */
      this.filterRows(this.hot);
    },
    /*  */
    listenScroll() {
      let wtHolder = document.getElementsByClassName('wtHolder')[0]
      wtHolder.addEventListener('scroll', (e) => {
        let scrollTop = wtHolder.scrollTop;
        let scrollLeft = wtHolder.scrollLeft;
        let paload = {
          scrollType: 'position',
          type: this.type,
          scrollTop: scrollTop,
          scrollLeft: scrollLeft
        }
        this.$store.commit('setScroll', paload);
        this.hot.destroyEditor()//
      });
    },
    /* dgm_name  */
    filter_by_dgmName() {
      let data = []
      if (!sessionStorage.getItem('dgm_id')) return; /*   */
      let dgm_ids = JSON.parse(sessionStorage.getItem('dgm_id'))
      if (dgm_ids.length == 0) return /*  */
      dgm_ids.forEach(dgm_id => {
        this.data.forEach(position => {
          if (position.dgm_id == dgm_id) {
            data.push(position)
          }
        })
      })
      this.data = data
    },
    //
    filterRows(hot){
      const filtersPlugin = hot.getPlugin('filters');
      if(sessionStorage.getItem('filter_dgm_name')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_dgm_name'));
        filterArr.forEach(condition =>{
          filtersPlugin.addCondition(2,condition.conditions[0].name,condition.conditions[0].args,condition.operations);
        });
        filtersPlugin.filter();
      }
      if(sessionStorage.getItem('filter_mac_name')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_mac_name'));
        filterArr.forEach(condition =>{
          filtersPlugin.addCondition(condition.column,condition.conditions[0].name,condition.conditions[0].args,condition.operations);
        });
        filtersPlugin.filter();
      }
      if(sessionStorage.getItem('filter_tree_name')){
        let filterArr =  JSON.parse(sessionStorage.getItem('filter_tree_name'));
        filterArr.forEach(condition =>{
          filtersPlugin.addCondition(condition.column,condition.conditions[0].name,condition.conditions[0].args,condition.operations);
        });
        filtersPlugin.filter();
      }
    },
    //data
    getData() {
      return this.data
    },
    /* hot */
    getHot() {
      return this.hot
    }
  }
}
