//
function setHeaderFrequency(headers, type) {
  //
  if (type == 3|| type == 15 || type == 4 || type == 8 || type == 16 || type == 9 || type == 6 || type == 11 || type == 12 || type == 13 || type == 17) {
    let obj0 = [], obj1 = [];
    for (let i = 1; i <= 8; i++) {
      obj0.push({ label: `${i}`, colspan: 5 })
      obj1.push(...[`${i}`, `${i}-1`, `${i}-1`,`${i}-2`, `${i}-2`, `${i}(Hz)`, `${i}(Hz)`])
    }
    // obj1.push('')
    // headers[0].push(...obj0)
    headers.push(...obj1)
  }

}










