//columns
export function setColumns(type, data) {
  let columns
  if (type == 200) { //
    columns = [
      {
        data: `t_name`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].t_sources
      },
      {
        data: `mac_me`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].macName_sources,

      },
      {
        data: `dgm_name`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].dgm_sources,
      },
      { data: `pos_name` }, //

      {
        data: `baseInfo.method_val`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.method_sources
      },
      {
        data: `pos_activity_val`, //
        editor: 'select',
        selectOptions: row => data[row].pos_activity_sources
      },
      {
        data: `baseInfo.capture_byId`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.capture_sources
      },
      {
        data: `baseInfo.capture_chName`, //
        editor: 'select',
        readOnly: true,
        selectOptions: row => data[row].baseInfo.capture_chName_sources
      },

    ]
    return columns
  }
  // 
  columns = [
    {
      data: `t_name`, //
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].t_sources
    },
    {
      data: `mac_me`, //
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].macName_sources,

    },

    {
      data: `dgm_name`, //
      editor: 'select',
      readOnly: true,
      selectOptions: row => data[row].dgm_sources,
    },
    { data: `pos_name` }, //
    // {
    //     data: `pos_type_name`, //
    // },
    {
      data: `ch_name`, //
      editor: 'select',
      selectOptions: row => data[row].ch_sources
    },
    {
      data: `pos_activity_val`, //
      editor: 'select',
      selectOptions: row => data[row].pos_activity_sources
    },
    {
      data: `isalarm_val`, //
      editor: 'select',
      selectOptions: row => data[row].isalarm_sources
    },
  ]
  columns.push(...selectColumns(parseInt(type), data))
  //
  setColumnFrequency(columns, type)
  return columns
}
