//
function setColumnFrequency(col, type) {
  if (type == 3 || type == 15 || type == 4 || type == 8 || type == 16 || type == 9 || type == 6 || type == 11 || type == 12 || type == 13 || type == 17) {
    let optfreq_type_source = [];
    let band_factor_type_source = [];
    if(type == 17){
      optfreq_type_source = [''];
      band_factor_type_source = [''];
    }
    else{
      optfreq_type_source = ['', '', ''];
      band_factor_type_source = ['', '', ''];
    }
    for (let i = 0; i <= 7; i++) {
      col.push(...[{
        data: `baseInfo.band${i}.dwOpt_freq_selected`,
        type: 'checkbox',
        checkedTemplate: '1',
        uncheckedTemplate: '0',
      },
      { data: `baseInfo.band${i}.dwOpt_freq_name` },
      {
        data: `baseInfo.band${i}.optfreq_type_val`,
        editor: 'select',
        selectOptions: optfreq_type_source
      },
      { data: `baseInfo.band${i}.band_factor_name` },
      {
        data: `baseInfo.band${i}.band_factor_type_val`,
        editor: 'select',
        selectOptions: band_factor_type_source
      },
      { data: `baseInfo.band${i}.dwOpt_freq_begin` },
      { data: `baseInfo.band${i}.dwOpt_freq_end` }]
      )
    }
  }
}
